var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"curso-main-container pb-3"},[_c('BannerInterno',{attrs:{"subTitulo":'1. Afectaciones directas al patrimonio: el hurto y la estafa'}}),_c('div',{staticClass:"container tarjeta tarjeta--blanca p-4 p-md-5 overflow-hidden"},[_vm._m(0),_c('div',{staticClass:"bg-full-width bg-fondo-slider mb-5"},[_c('div',{staticClass:"p-4 p-md-5"},[_c('SlyderA',{staticClass:"bg-white",attrs:{"tipo":"b"}},[_c('div',{staticClass:"row align-items-center p-4 p-md-5"},[_c('div',{staticClass:"col-lg-5 order-2"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema4/2.png")}})])]),_c('div',{staticClass:"col-lg-7 order-1 mb-3 mb-lg-0"},[_c('h5',[_vm._v("El hurto")]),_c('p',[_vm._v("Regulado en el Artículo 239 y siguientes del Código Penal, implica una conducta activa de apoderamiento de un bien mueble ajeno sin consentimiento de su propietario y con ánimo de lucro. Es decir, se concreta cuando alguien toma algo que no le pertenece, sin violencia o engaño, con la intención de incorporarlo de forma definitiva a su propio patrimonio o al de un tercero. Este delito no requiere que el apoderamiento se prolongue en el tiempo, basta con que se interrumpa la tenencia legítima y el agente actúe con dolo, es decir, con plena conciencia y voluntad de cometer la apropiación ilegítima. Por ejemplo, si un individuo sustrae una bicicleta estacionada en vía pública sin autorización del propietario, se configura un delito de hurto, independientemente de que la venda o simplemente la utilice.")])])]),_c('div',{staticClass:"row align-items-center p-4 p-md-5"},[_c('div',{staticClass:"col-lg-5 order-2"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema4/3.png")}})])]),_c('div',{staticClass:"col-lg-7 order-1 mb-3 mb-lg-0"},[_c('h5',[_vm._v("La estafa")]),_c('p',[_vm._v("Se encuentra prevista en el Artículo 246 del mismo Código. A diferencia del hurto, que recae sobre una acción física de apoderamiento, la estafa se configura mediante el empleo de maniobras engañosas para inducir a error a otra persona y obtener de ella una disposición patrimonial en perjuicio propio o de un tercero. Este delito requiere, por tanto, no solo el resultado económico negativo para la víctima, sino también la existencia de un ardid o engaño que haya sido decisivo en la conducta de quien fue inducido a desprenderse de su bien. Por ejemplo, si una persona ofrece la venta de un vehículo inexistente mediante documentos falsificados y logra que la víctima transfiera una suma de dinero bajo la creencia de que adquirirá el bien, se materializa el tipo penal de estafa.")])])])])],1)]),_vm._m(1),_c('p',{staticClass:"mb-5"},[_vm._v("En síntesis, las conductas que afectan directamente el patrimonio económico, como el hurto y la estafa, comprometen no solo la seguridad de los bienes individuales, sino también la confianza en las relaciones sociales, comerciales y jurídicas. Por ello, el ordenamiento penal colombiano establece mecanismos de control y sanción que buscan prevenir, reprimir y reparar este tipo de afectaciones, protegiendo así el orden económico y jurídico de la sociedad.")]),_vm._m(2),_vm._m(3),_c('div',{staticClass:"row align-items-start mb-5"},[_vm._m(4),_c('div',{staticClass:"col-lg-8"},[_c('AcordionA',{attrs:{"tipo":"b"}},[_c('div',{staticClass:"div",attrs:{"titulo":"El hurto simple"}},[_c('p',[_vm._v("Consagrado en el Artículo 239, se configura cuando una persona se apodera de un bien mueble que pertenece a otro, sin que medie violencia ni intimidación sobre las personas o sobre las cosas. En este tipo penal, el autor actúa con dolo directo, consciente de que se está apropiando de lo ajeno, y guiado por un ánimo de lucro. Para que exista hurto simple, no es necesario el uso de fuerza o amenaza; basta con que se materialice el desapoderamiento sin consentimiento del legítimo propietario. Por ejemplo, si una persona en un supermercado aprovecha un descuido y se guarda productos en su ropa para salir sin pagarlos, comete hurto simple. La acción es discreta, no violenta, pero implica un apoderamiento con clara intencionalidad. También es hurto simple, cuando un trabajador de una oficina se apropia de equipos, herramientas o dinero en efectivo pertenecientes a la empresa, sin ejercer violencia alguna, pero con el propósito de quedarse con esos bienes de forma definitiva.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"El hurto calificado"}},[_c('p',[_vm._v("Tipificado en el Artículo 240, es una modalidad más grave del delito, y, por tanto, se sanciona con mayor severidad. Esta calificación surge cuando el apoderamiento se realiza en circunstancias especialmente reprochables por su peligrosidad o lesividad, como el uso de violencia sobre las personas o las cosas, la utilización de medios engañosos, el actuar en grupo o cuadrilla, o el ingreso a lugares sagrados o especialmente protegidos. En estos casos, el legislador ha entendido que el daño al bien jurídico protegido, es decir, al patrimonio económico, se agrava no solo por el valor del bien hurtado, sino por el modo en que se vulnera la seguridad o la dignidad de la víctima (Agudo Fernández & Jaén Vallejo, 2018).")])])])],1)]),_vm._m(5),_vm._m(6),_vm._m(7),_c('p',{staticClass:"mb-5"},[_vm._v("En cuanto a la sanción, el hurto simple es reprimido con penas de dos a seis años de prisión, dependiendo del valor del bien hurtado y otros factores. Pero si concurren las circunstancias de calificación descritas por el Artículo 240, las penas oscilan entre seis y catorce años, con posibilidad de agravarse, según el Artículo 241 si existen múltiples circunstancias agravantes (Agudo Fernández & Jaén Vallejo, 2018).")]),_vm._m(8),_vm._m(9),_c('p',{staticClass:"mb-5"},[_vm._v("La estafa es una de las figuras más representativas de las afectaciones patrimoniales en el ámbito penal, caracterizada por el uso del engaño como medio para obtener beneficios económicos indebidos.")]