var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-2 mb-3 mb-lg-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema4/1.svg"),"data-aos":"zoom-in"}})])]),_c('div',{staticClass:"col-lg-10"},[_c('p',{attrs:{"data-aos":"fade-left"}},[_vm._v("El patrimonio económico es uno de los bienes jurídicos más relevantes, protegidos por el Derecho Penal. Se entiende como el conjunto de bienes, derechos y obligaciones susceptibles de valoración económica que una persona puede poseer, adquirir o transmitir dentro del marco legal. Cualquier conducta que atente contra esa integridad patrimonial, mediante el desapoderamiento ilegítimo de bienes ajenos o el aprovechamiento indebido mediante el engaño, constituye una infracción penal. En esta unidad se estudian dos de las figuras más representativas de las afectaciones patrimoniales directas. El hurto y la estafa, ambas consagradas y desarrolladas en el Código Penal colombiano, Ley 599 del 2000:")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-4 mb-3 mb-lg-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema4/4.png"),"data-aos":"zoom-in"}})])]),_c('div',{staticClass:"col-lg-8"},[_c('p',{staticClass:"mb-3",attrs:{"data-aos":"fade-left"}},[_vm._v("Ambas figuras tienen como eje común la afectación al patrimonio ajeno, pero se distinguen por la forma en que se produce dicha afectación. En el hurto, la disposición patrimonial se da sin participación de la víctima, pues esta no consiente el desapoderamiento. En la estafa, en cambio, la víctima sí participa en la entrega del bien, pero lo hace inducida en error por las maniobras fraudulentas del autor. Esta diferencia es esencial para la adecuada tipificación del comportamiento en el proceso penal.")]),_c('div',{staticClass:"bg-color-5 p-4",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"row align-items-start"},[_c('div',{staticClass:"col-lg-auto"},[_c('img',{staticClass:"mx-auto",staticStyle:{"max-width":"90px"},attrs:{"src":require("@/assets/curso/tema4/5.svg")}})]),_c('div',{staticClass:"col-lg"},[_c('p',{staticClass:"mb-0"},[_vm._v("En Colombia, tanto el hurto como la estafa pueden agravarse si se configuran circunstancias especiales, como el uso de medios violentos, el hecho de actuar en grupo, el aprovechamiento de condiciones de confianza o la utilización de sistemas informáticos. El Código Penal contempla estos agravantes para aumentar la pena en atención al mayor reproche social que generan estos comportamientos, especialmente cuando afectan a personas vulnerables o bienes de especial valor económico, cultural o sentimental.")])])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"titulo-segundo",attrs:{"id":"t_1_1","data-aos":"flip-up"}},[_c('h2',[_c('span',[_vm._v("1.1")]),_vm._v(" Hurto simple y calificado")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-4 order-lg-2 mb-3 mb-lg-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema4/7.png"),"data-aos":"zoom-in"}})])]),_c('div',{staticClass:"col-lg-8 order-lg-1"},[_c('div',{staticClass:"bg-color-11 p-4",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"row align-items-center"},[_c('div',{staticClass:"col-lg-auto mb-3 mb-lg-0"},[_c('img',{staticClass:"mx-auto",staticStyle:{"max-width":"170px"},attrs:{"src":require("@/assets/curso/tema4/6.svg")}})]),_c('div',{staticClass:"col-lg"},[_c('p',{staticClass:"mb-0"},[_vm._v("En el marco del Derecho Penal colombiano, el hurto es uno de los delitos más comunes y representa una afectación directa al patrimonio económico de las personas. Este delito está regulado por los Artículos 239 y 240 del Código Penal, Ley 599 del 2000, donde se establece su configuración típica, sus elementos constitutivos y las variantes agravadas. ")])])])]),_c('div',{staticClass:"bg-color-1 p-4",attrs:{"data-aos":"fade-left"}},[_c('p',{staticClass:"mb-0"},[_vm._v("La esencia del hurto radica en el apoderamiento ilegítimo de un bien ajeno con ánimo de lucro, es decir, con la intención de incorporarlo de manera permanente o definitiva al patrimonio del autor o de un tercero. No se trata simplemente de tomar lo ajeno, sino de hacerlo con conciencia de su ilicitud, sin autorización del propietario y con la finalidad de obtener un provecho indebido (Agudo Fernández & Jaén Vallejo, 2018).")])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"col-lg-4 mb-3 mb-lg-0"},[_c('figure',[_c('img',{staticClass:"mb-4 mb-lg-0",attrs:{"src":require("@/assets/curso/tema4/8.png"),"alt":""}})])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"bg-color-6 mb-5",attrs:{"data-aos":"fade-up"}},[_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-auto order-2"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema4/9.svg"),"alt":""}})])]),_c('div',{staticClass:"col-lg order-1 mb-3 mb-lg-0"},[_c('div',{staticClass:"p-4"},[_c('p',{attrs:{"data-aos":"fade-up"}},[_vm._v("Un ejemplo clásico de hurto calificado, es el robo de objetos al interior de un vehículo tras romper una ventana con una piedra. Aquí hay una violencia sobre la cosa, el vehículo, lo cual agrava la conducta. Otro caso es el de una persona que ingresa a una casa con llaves hurtadas y sustrae bienes, mientras los residentes están ausentes. También se configura hurto calificado cuando el delito se comete en grupo, como en los casos de pandillas que ingresan a establecimientos comerciales para apoderarse de mercancía, repartiendo roles entre quienes vigilan, intimidan o sustraen los objetos. ")]),_c('p',{staticClass:"mb-0"},[_vm._v("A diferencia del hurto simple, en el hurto calificado el uso de medios peligrosos, el ataque sorpresivo, o el aprovechamiento de condiciones vulnerables, incrementan el reproche penal.")])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"bg-full-width bg-color-4 mb-5"},[_c('div',{staticClass:"px-4 px-md-5 py-4"},[_c('div',{staticClass:"row align-items-center"},[_c('div',{staticClass:"col-lg-auto"},[_c('img',{staticClass:"mx-auto",staticStyle:{"max-width":"90px"},attrs:{"src":require("@/assets/curso/tema4/10.svg")}})]),_c('div',{staticClass:"col-lg text-white"},[_c('p',{staticClass:"mb-0"},[_vm._v("Otra circunstancia calificada es la comisión del hurto en lugares especiales, como templos religiosos, hospitales, centros educativos o instituciones de beneficencia. Por ejemplo, hurtar dinero de las limosnas en una iglesia, además de implicar un apoderamiento indebido, ofende un lugar de especial respeto social, por lo que la conducta adquiere una mayor gravedad.")])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-4 mb-3 mb-lg-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema4/11.png"),"data-aos":"zoom-in"}})])]),_c('div',{staticClass:"col-lg-8"},[_c('div',{staticClass:"bg-color-2 py-4 px-5",attrs:{"data-aos":"fade-left"}},[_c('p',{staticClass:"mb-0"},[_vm._v("Desde el punto de vista técnico, ambos tipos penales comparten los elementos estructurales básicos. La conducta dolosa, el objeto mueble ajeno, el ánimo de lucro y la falta de consentimiento del titular. Sin embargo, en el hurto calificado se suman elementos que elevan el nivel de antijuridicidad material y exigen del Estado una respuesta penal más contundente. Esto se traduce en un aumento de la pena privativa de la libertad, que puede alcanzar entre seis y catorce años, e incluso incrementarse aún más, si concurren circunstancias adicionales, como la reincidencia o el hurto de bienes de alto valor económico.")])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-4 mb-3 mb-lg-0"},[_c('div',{staticClass:"bg-color-10 tarjeta p-3 h-100",attrs:{"data-aos":"fade-left"}},[_c('p',{attrs:{"data-aos":"fade-left"}},[_vm._v("Desde la perspectiva jurisprudencial, la Corte Suprema de Justicia ha reiterado en múltiples decisiones que, para diferenciar correctamente entre hurto simple y calificado, se debe analizar no solo la conducta delictiva, sino también las condiciones del contexto en que fue cometida. El análisis del dolo, la modalidad del apoderamiento, el impacto sobre la víctima y la naturaleza del bien sustraído, son elementos claves que permiten determinar el tipo penal aplicable.")])])]),_c('div',{staticClass:"col-lg-4 mb-3 mb-lg-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema4/12.png"),"data-aos":"zoom-in"}})])]),_c('div',{staticClass:"col-lg-4 mb-3 mb-lg-0"},[_c('div',{staticClass:"bg-color-1 tarjeta p-3 h-100",attrs:{"data-aos":"fade-left"}},[_c('p',{attrs:{"data-aos":"fade-left"}},[_vm._v("En conclusión, el hurto simple y el hurto calificado son figuras jurídicas que responden a una misma finalidad de tutela del patrimonio, pero que se distinguen por el grado de agresividad y la forma en que se comete el delito. La correcta tipificación tiene efectos directos sobre la responsabilidad penal del autor, la dosificación de la pena y las posibilidades de aplicación de subrogados penales o mecanismos de justicia restaurativa. ")])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"titulo-segundo",attrs:{"id":"t_1_2","data-aos":"flip-up"}},[_c('h2',[_c('span',[_vm._v("1.2")]),_vm._v(" Estafa")])])},function () {var _vm=this;var _h=_vm.$createEleme